*, *::before, *::after {
  box-sizing: border-box;
  font-family: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
}

html:focus-within {
  scroll-behavior: smooth;
}

a, a:visited, a:hover {
  text-decoration: none;
  color: inherit;
}

input,
button,
textarea,
select {
  font: inherit;
}

ul, li {
  list-style: none;
}

button {
  background: transparent;
  border: none;
  color: inherit;
  cursor: pointer;
}

body {
  font-family: var(--ff);
  font-size: var(--fs-small);
  font-weight: var(--fw-reg);
  line-height: 1.5;
  background-color: var(--bg-clr);
  color: var(--text-clr);
  background-image: var(--bg-mobile);
  background-repeat: no-repeat;
  background-size: 100%;
}
@media (min-width: 40em) {
  body {
    font-size: var(--fs-body);
  }
}
@media (min-width: 71.875em) {
  body {
    background-image: var(--bg-desktop);
  }
}

.container {
  max-width: 33.75rem;
  margin: 0 auto;
  padding: 1.5625rem;
  padding-top: 3rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    width: 100vw;
    height: 100vh;
    margin-top: 80px;
  }
}

.flex {
  display: flex;
}

.jc-sb {
  justify-content: space-between;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --ff:"Josefin Sans", sans-serif;
  --fs-xlarge: 2.5rem;
  --fs-large: 1.375rem;
  --fs-body: 1.125rem;
  --fs-med: 1rem;
  --fs-small: .875rem;
  --fs-xsmall: .75rem;
  --fs-xxsmall: .625rem;
  --fw-reg:400;
  --fw-bold:700;
  --accent-clr-BrightBlue: hsl(220, 98%, 61%);
  --gradient-clrs: hsl(192, 100%, 67%), hsl(280, 87%, 65%);
  --checkbox-bg-clr: linear-gradient(135deg, var(--gradient-clrs));
  --focus-clr-pink:hsl(280, 87%, 65%);
  --VeryLightGray: hsl(0, 0%, 98%);
  --VeryLightGrayishBlue: hsl(236, 33%, 92%);
  --LightGrayishBlue: hsl(233, 11%, 84%);
  --DarkGrayishBlue: hsl(236, 9%, 61%);
  --VeryDarkGrayishBlue: hsl(235, 19%, 35%);
  --White: hsl(0, 0%, 100%);
  --VeryDarkBlue: hsl(235, 21%, 11%);
  --VeryDarkDesaturatedBlue: hsl(235, 24%, 19%);
  --LightGrayishBlue: hsl(234, 39%, 85%);
  --LightGrayishBlue-hover: hsl(236, 33%, 92%);
  --DarkGrayishBlue: hsl(234, 11%, 52%);
  --VeryDarkGrayishBlue: hsl(233, 14%, 35%);
  --VeryDarkGrayishBlue: hsl(237, 14%, 26%);
}

:root[data-theme=light] {
  --bg-clr: var(--VeryLightGray);
  --list-bg-clr: var(--White);
  --text-clr-xlight: var(--LightGrayishBlue);
  --text-clr-light: var(--DarkGrayishBlue);
  --text-clr: var(--VeryDarkGrayishBlue);
  --text-clr-filter: var(--text-clr-light);
  --bg-mobile: url(/images/bg-mobile-light.jpg) ;
  --bg-desktop: url(/images/bg-desktop-light.jpg) ;
  --themeToggle: url(/images/icon-moon.svg) ;
  --accent-clr-Gray: var(--VeryLightGrayishBlue);
}

:root[data-theme=dark] {
  --bg-clr: var(--VeryDarkBlue);
  --list-bg-clr: var(--VeryDarkDesaturatedBlue);
  --text-clr-xlight: var(--DarkGrayishBlue);
  --text-clr-light: var(--VeryDarkGrayishBlue);
  --text-clr: var(--LightGrayishBlue-hover);
  --text-clr-filter: var(--DarkGrayishBlue);
  --bg-mobile: url(/images/bg-mobile-dark.jpg) ;
  --bg-desktop: url(/images/bg-desktop-dark.jpg) ;
  --themeToggle: url(/images/icon-sun.svg) ;
  --accent-clr-Gray: var(--VeryDarkGrayishBlue);
}

@media (prefers-color-scheme: dark) {
  :root[data-theme=dark] {
    --bg-clr: var(--VeryDarkBlue);
    --list-bg-clr: var(--VeryDarkDesaturatedBlue);
    --text-clr-xlight: var(--DarkGrayishBlue);
    --text-clr-light: var(--LightGrayishBlue);
    --text-clr: var(--LightGrayishBlue-hover);
    --text-clr-filter: var(--DarkGrayishBlue);
    --bg-mobile: url(/images/bg-mobile-dark.jpg) ;
    --bg-desktop: url(/images/bg-desktop-dark.jpg) ;
    --themeToggle: url(/images/icon-sun.svg) ;
    --accent-clr-Gray: var(--VeryDarkGrayishBlue);
  }
}
.header {
  align-items: center;
  line-height: 1.7;
  margin-bottom: 1.5625rem;
}
@media (min-width: 40em) {
  .header {
    margin-bottom: 2.1875rem;
  }
}
.header__title {
  font-size: var(--fs-large);
  text-transform: uppercase;
  color: var(--White);
  letter-spacing: 0.5rem;
}
@media (min-width: 40em) {
  .header__title {
    font-size: var(--fs-xlarge);
    letter-spacing: 1rem;
  }
}
.header__toggle {
  height: 1.375rem;
  width: 1.375rem;
}
@media (min-width: 40em) {
  .header__toggle {
    height: 1.75rem;
    width: 1.75rem;
  }
}
.header .themeButton {
  background-image: var(--themeToggle);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.todo {
  flex-direction: column;
  gap: 1rem;
}
.todo__new-todo form {
  position: relative;
}
.todo .customCheckIcon {
  content: "";
  position: absolute;
  top: 0.875rem;
  left: 1.25rem;
  display: inline-block;
  width: 1.375rem;
  height: 1.375rem;
  background: var(--list-bg-clr);
  border: 1px solid var(--accent-clr-Gray);
  border-radius: 100%;
}
@media (min-width: 40em) {
  .todo .customCheckIcon {
    top: 1.0625rem;
  }
}
.todo__new-todo textarea {
  background-color: var(--list-bg-clr);
  color: var(--text-clr);
  border: none;
  border-radius: 0.4375rem;
  width: 100%;
  min-height: auto;
  min-width: 160px;
  max-height: auto;
  padding: 1rem 3.75rem 0.8125rem;
  word-break: break-all;
  resize: none;
  cursor: pointer;
}
.todo__new-todo textarea::placeholder {
  min-width: 160px;
  word-break: break-all;
}
.todo__new-todo textarea::-webkit-scrollbar {
  display: none;
}
.todo__new-todo, .todo__list {
  box-shadow: 0 13px 35px -8px rgba(0, 0, 0, 0.1);
}
.todo__list {
  background-color: var(--list-bg-clr);
  border: none;
  border-radius: 0.4375rem;
  width: 100%;
  margin-bottom: 1rem;
}
.todo__task, .todo__options {
  padding: 1.1875rem 1rem 0.6875rem 1.25rem;
  position: relative;
}
@media (min-width: 40em) {
  .todo__task, .todo__options {
    padding: 1rem 1rem 0.3125rem 1.25rem;
  }
}
.todo__task:not(:first-child)::after, .todo__options::after {
  content: "";
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  height: 1px;
  background: var(--text-clr-filter);
  opacity: 0.1;
}
.todo__task .taskDescription {
  background-color: var(--list-bg-clr);
  color: var(--text-clr);
  border: none;
  border-radius: 0.4375rem;
  width: 100%;
  min-height: auto;
  max-height: auto;
  padding: 0 2.75rem 0 2.5rem;
  word-break: break-all;
  resize: none;
}
.todo__task textarea:focus {
  outline: transparent;
}
.todo__task textarea::-webkit-scrollbar {
  display: none;
}
.todo__task {
  justify-content: space-between;
  align-items: center;
}
.todo__task .customCheckbox {
  order: 2;
  width: 100%;
}
.todo__task input[type=checkbox] + .customCheckbox {
  display: block;
  position: relative;
  cursor: pointer;
}
.todo__task input[type=checkbox] + .customCheckbox::before {
  content: "";
  position: absolute;
  top: -1px;
  display: inline-block;
  width: 1.375rem;
  height: 1.375rem;
  background: var(--list-bg-clr);
  border: 1px solid var(--accent-clr-Gray);
  border-radius: 100%;
  margin-right: 0.8125rem;
}
@media (min-width: 40em) {
  .todo__task input[type=checkbox] + .customCheckbox::before {
    top: 2px;
  }
}
.todo__task input[type=checkbox] + .customCheckbox:hover::before {
  background: linear-gradient(var(--list-bg-clr), var(--list-bg-clr)) padding-box, var(--checkbox-bg-clr) border-box;
  border-radius: 100%;
  border: 1px solid transparent;
}
.todo__task input[type=checkbox]:checked + .customCheckbox::before {
  background: var(--checkbox-bg-clr);
  border: 1px solid var(--checkbox-bg-clr);
}
.todo__task input[type=checkbox]:checked + .customCheckbox::after {
  content: "";
  position: absolute;
  left: 0.375rem;
  top: 0.3125rem;
  background-image: url(/images/icon-check.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 0.625rem;
  height: 0.625rem;
}
@media (min-width: 40em) {
  .todo__task input[type=checkbox]:checked + .customCheckbox::after {
    top: 0.5rem;
  }
}
.todo__task input[type=checkbox]:checked + .customCheckbox {
  color: var(--text-clr-xlight);
}
.todo__task input[type=checkbox]:checked + label textarea {
  color: var(--text-clr-xlight);
  text-decoration: line-through;
}
.todo__task input[type=checkbox]:focus + .customCheckbox::before {
  box-shadow: 0 0px 8px #5e9ed6;
}
.todo .deleteButton {
  background-image: url(/images/icon-cross.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  order: 3;
  position: absolute;
  right: 1.0625rem;
  top: 1.5rem;
  width: 0.875rem;
  height: 0.875rem;
  margin-left: auto;
  opacity: 1;
}
@media (min-width: 71.875em) {
  .todo .deleteButton {
    opacity: 0;
  }
  .todo .deleteButton:focus {
    opacity: 1;
  }
}
.todo__list li:hover .deleteButton {
  opacity: 1;
}
.todo__options {
  color: var(--text-clr-light);
  font-size: var(--fs-xsmall);
  line-height: 2.3;
}
.todo__options {
  color: var(--text-clr-filter);
}
.todo__filter {
  justify-content: center;
  background: var(--list-bg-clr);
  color: var(--text-clr-filter);
  padding: 1rem 0 0.8125rem 0;
  border: transparent;
  border-radius: 0.4375rem;
  font-size: var(--fs-small);
  font-weight: var(--fw-bold);
}
@media (min-width: 40em) {
  .todo__filter {
    background: transparent;
    position: relative;
    width: 50%;
    top: -5rem;
    left: 7.8125rem;
  }
}
.todo__filter label:hover, .todo .clearButton:hover {
  color: var(--text-clr);
  cursor: pointer;
}
.todo__filter label:not(:last-child) {
  margin-right: 1.375rem;
}
.todo__filter input[type=radio]:checked + label {
  color: var(--accent-clr-BrightBlue);
}
.todo__filter input[type=radio]:focus + label {
  color: var(--focus-clr-pink);
}